.browse-container{
    background-color: rgb(166, 236, 166);
}

.anim{
    transition-duration: 0.1s;
}

.browse-header{
    background-image: linear-gradient(0deg, #008000b0, #008000b0),
    url("../../../public/browse-header.jpg");
    background-size: cover;
    height:40vh;
}

.browse-search-icon{
    position: absolute;
    top: 13px;
    left: 18px;
    font-size: 20px;
}

.browse-search-container{
    position: relative;
    margin: auto;
}


@media screen and (min-width: 768px) {
    .browse-search-container{
        width: 75%;
    }
    
}